.projects {
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 100%;
    justify-content: center;
    padding: 2rem;
    max-width: 1000px;

}

.projects-header {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
}




.class-select-show {
    width: fit-content;
    background: var(--foreground);
    position: absolute;
    right: 0;
    display: flex;
    flex-direction: column;
    top: 0;
}

.class-select-hide {
    width: fit-content;
    background: var(--foreground);
}
 

.page-line {
    width: 100%;
}

.project-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.dropdown {
    position: absolute;
    right: 0;
    bottom: -160px;
    background: var(--foreground);
    padding: 0.75rem;
    display: flex;
    flex-direction: column;
    gap: 10px;
    flex-wrap: nowrap;
    border: 1px solid var(--border);
    border-radius: var(--border-radius);
    width: 300px;
}


.class-select-container  {
    position: absolute;
    width: 300px;
    display: flex;
    gap: 10px;
    flex-direction: column;
    flex-wrap: nowrap;
    right: -15px;
    top: -30px;
    overflow-y: hidden;
    overflow-x: visible;
    padding: 1rem;
    padding-top: 0.75rem;
    border-radius: var(--border-radius);
}


.ac-header {
    position: absolute;
    top: -10px;
    left: 5px;
}
